const 강아지용품데이터 = require('./data.js')

//간식먹기
  function 간식먹기(이름, 빼는수량) {
    console.log(모든간식데이터)
    const 모든간식데이터 = 강아지용품데이터.간식
    for (let i = 0; i < 모든간식데이터.length; i++) {
      const 각간식데이터 = 모든간식데이터[i]
        if(각간식데이터.이름 === 이름) {
          각간식데이터.수량 = 각간식데이터.수량 - 빼는수량
        }
    }  
  }
    // 간식먹기('오리', 3)

//간식추가
  function 간식추가(이름, 유통기한, 추가수량, 추가재료, 제조사) {
    const 모든간식데이터 = 강아지용품데이터.간식
    for (let i = 0; i < 모든간식데이터.length; i++) {
      const 개별간식데이터 = 모든간식데이터[i]
      if(개별간식데이터.이름 === 이름) {
        return 개별간식데이터.수량 = 개별간식데이터.수량 + 추가수량
      } else {  
        return 강아지용품데이터.간식.push({이름, 유통기한, 추가수량, 추가재료, 제조사})
      }  
    }
  }
    // 간식추가('말린망고', 10, 5, '망고', '아이엠필리핀')

// 모든간식출력
  function 모든간식출력하기() {
    강아지용품데이터.간식.forEach((separate) => console.log('이름 :', separate.이름, '/ 수량 :', separate.수량))
  }
    // 모든간식출력하기()

// 모든장난감출력
  function 모든장난감출력하기() {
    const 모든장난감데이터 = 강아지용품데이터.장난감
    const subCategoryList = Object.keys(모든장난감데이터)
    subCategoryList.forEach(장난감이름 => console.log(장난감이름))
  }
    // 모든장난감출력하기()

// 특정서브카테고리의 장난감출력
  function 특정서브카테고리의장난감출력하기(장난감서브카테고리이름) {
    const 모든장난감데이터 = 강아지용품데이터.장난감
    const subCategoryList = Object.keys(모든장난감데이터)
    for (let i = 0; i < subCategoryList.length; i++) {
      const subCategory = subCategoryList[i]
      const subCategoryData = 모든장난감데이터[subCategory]
      if (subCategory === 장난감서브카테고리이름) {
        console.log(subCategoryData)
      }
    }
  }
   // 특정서브카테고리의장난감출력하기('터그')

// 1111장난감추가하기 
  function 장난감추가하기(이름, 수량, 가격, 소재, 판매처) {
    const subCategoryList = Object.keys(강아지용품데이터.장난감)
    for (let i = 0; i < subCategoryList.length; i++) {
      const subCategory = subCategoryList[i]
      const toyDataList = 강아지용품데이터.장난감[subCategory]
    for (let j = 0; j < toyDataList.length; j++) {
      const toyData = toyDataList[j]
      if(toyData.이름 === 이름) {
        return toyData.수량 = toyData.수량 + 수량
      } else {
        toyDataList = toyDataList.push({이름, 수량, 가격, 소재, 판매처})
      } 
    }
    }
  }
   // console.log(강아지용품데이터.장난감)
   // 장난감추가하기('고양이터그', 8, 10000, '실크', '고먐미나라')

  
