  function 장난감서브카테고리추가하기(추가카테고리, 이름, 수량, 가격) {
    강아지용품데이터.장난감[추가카테고리] = [{이름, 수량, 가격}] 
    
  }
   // console.log(강아지용품데이터.장난감)
   // 장난감서브카테고리추가하기('집게', '오리집게', 3, 1000)


